/* --------------------------------------------------------------
 * = 提示信息
 * ----------------------------------------------------------- */
.alert {
  padding: 10px 15px;
  margin-bottom: 1em;
  background: #fcf8e3;
  color: #c09853;
}
.alert a {
  color: #963;
}
.alert-header {
  font-size: 18px;
  color: inherit;
  vertical-align: middle;
}
.alert-content {
  padding-left: 1.5em;
}
.alert-ok {
  background: #dff0d8;
  color: #3c763d;
}
.alert-error {
  background: #f2dede;
  color: #b94a48;
}
.alert i:before {
  vertical-align: text-top;
}
.ui-box {
  border: 1px solid #ccc;
  zoom: 1;
  font-size: 12px;
  margin: 0;
  padding: 0;
  border-bottom: none;
}
.ui-box .ui-media:not(:last-child) {
  margin-bottom: 10px;
}
.ui-box:after {
  clear: both;
  content: " ";
  display: block;
  font-size: 0;
  height: 0;
  visibility: hidden;
}
.ui-box-follow {
  border-top: none;
}
.ui-box-container {
  border-bottom: 1px solid #ccc;
}
.ui-box-content {
  padding: 10px;
}
.ui-box-head {
  border-bottom: 1px solid #ccc;
  position: relative;
  padding: 10px;
  height: 16px;
  line-height: 16px;
  zoom: 1;
}
.ui-box-head-title {
  color: #656565;
  font-size: 14px;
  font-weight: bold;
  float: left;
  display: inline;
  margin: 0;
  padding: 0;
}
.ui-box-head-more {
  float: right;
}
.ui-box-head-text {
  margin-left: 10px;
  color: #808080;
  float: left;
}
/* --------------------------------------------------------------
 * = 下拉
 * ----------------------------------------------------------- */
.dropit {
  list-style: none;
  padding: 0;
  margin: 0;
}
.dropit .dropit-trigger {
  position: relative;
}
.dropit .dropit-submenu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
  min-width: 150px;
  list-style: none;
  padding: 0;
  margin: 0;
}
.dropit .dropit-open .dropit-submenu {
  display: block;
}
.dropup,
.dropdown {
  position: relative;
}
.dropdown-toggle {
  *margin-bottom: -3px;
}
.dropdown-toggle:active,
.open .dropdown-toggle {
  outline: 0;
}
.caret {
  display: inline-block;
  width: 0;
  height: 0;
  vertical-align: top;
  border-top: 4px solid #000000;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
  content: "";
}
.dropdown .caret {
  margin-top: 8px;
  margin-left: 2px;
}
.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  _top: 1em;
  left: 0;
  z-index: 1000;
  float: left;
  min-width: 160px;
  padding: 5px 0;
  margin: 2px 0 0;
  list-style: none;
  background-color: #ffffff;
  border: 1px solid #dedede;
  border-right-width: 2px;
  border-bottom-width: 2px;
  -webkit-background-clip: padding-box;
  -moz-background-clip: padding;
  background-clip: padding-box;
}
.dropdown-menu.pull-right {
  right: 0;
  left: auto;
}
.dropdown-menu .divider {
  *width: 100%;
  height: 1px;
  margin: 9px 1px;
  *margin: -5px 0 5px;
  overflow: hidden;
  background-color: #e5e5e5;
  border-bottom: 1px solid #ffffff;
}
.dropdown-menu li a {
  display: block;
  padding: 3px 20px;
  clear: both;
  font-weight: normal;
  line-height: 20px;
  color: #000;
  white-space: nowrap;
}
.dropdown-menu li a:hover,
.dropdown-menu li a:focus,
.dropdown-submenu:hover a,
.dropdown-submenu:focus a {
  text-decoration: none;
  color: #ffffff;
  background-color: #0081c2;
  background-image: linear-gradient(to bottom, #0088cc, #0077b3);
  background-repeat: repeat-x;
}
.dropdown-menu .active a,
.dropdown-menu .active a:hover,
.dropdown-menu .active a:focus {
  color: #ffffff;
  text-decoration: none;
  outline: 0;
  background-color: #0081c2;
  background-image: linear-gradient(to bottom, #0088cc, #0077b3);
  background-repeat: repeat-x;
}
.dropdown-menu .disabled a,
.dropdown-menu .disabled a:hover,
.dropdown-menu .disabled a:focus {
  color: #999999;
}
.dropdown-menu .disabled a:hover,
.dropdown-menu .disabled a:focus {
  text-decoration: none;
  background-color: transparent;
  background-image: none;
  filter: progid:DXImageTransform.Microsoft.gradient(enabled=false);
  cursor: default;
}
.open {
  *z-index: 1000;
}
.open.dropdown-menu {
  display: block;
}
.dropdown-backdrop {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 990;
}
.pull-right .dropdown-menu {
  right: 0;
  left: auto;
}
.dropup .caret,
.navbar-fixed-bottom .dropdown .caret {
  border-top: 0;
  border-bottom: 4px solid #000000;
  content: "";
}
.dropup .dropdown-menu,
.navbar-fixed-bottom .dropdown .dropdown-menu {
  top: auto;
  bottom: 100%;
  margin-bottom: 1px;
}
.dropdown-submenu {
  position: relative;
}
.dropdown-submenu .dropdown-menu {
  top: 0;
  left: 100%;
  margin-top: -6px;
  margin-left: -1px;
}
.dropdown-submenu:hover .dropdown-menu {
  display: block;
}
.dropup .dropdown-submenu .dropdown-menu {
  top: auto;
  bottom: 0;
  margin-top: 0;
  margin-bottom: -2px;
}
.dropdown-submenu a:after {
  display: block;
  content: " ";
  float: right;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
  border-width: 5px 0 5px 5px;
  border-left-color: #cccccc;
  margin-top: 5px;
  margin-right: -10px;
}
.dropdown-submenu:hover a:after {
  border-left-color: #ffffff;
}
.dropdown-submenu.pull-left {
  float: none;
}
.dropdown-submenu.pull-left .dropdown-menu {
  left: -100%;
  margin-left: 10px;
}
.dropdown .dropdown-menu .nav-header {
  padding-left: 20px;
  padding-right: 20px;
}
.ui-list {
  margin: 0;
  padding: 10px;
  list-style: square inside;
}
.ui-list-list-item {
  line-height: 20px;
}
.ui-list-list-item a {
  line-height: 20px;
